// draw cube with three js
import * as THREE from 'three';
import {Interaction} from 'three.interaction';
const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(
    75,
    window.innerWidth / window.innerHeight,
    0.1,
    1000
);
const renderer = new THREE.WebGLRenderer();
renderer.setSize(window.innerWidth, window.innerHeight);
const interaction = new Interaction(renderer, scene, camera);
document.body.appendChild(renderer.domElement);

const geometry = new THREE.BoxGeometry(1, 1, 1);
const material = new THREE.MeshPhongMaterial({ color: 0x00ff00 });
const cube = new THREE.Mesh(geometry, material);
cube.cursor = 'pointer';
cube.position.set(0, 0, -5);
cube.rotateX(0.5);
cube.rotateY(0.5);
scene.add(cube)

const animate = function () {
    requestAnimationFrame(animate);
    renderer.render(scene, camera);
}

animate();

document.addEventListener('mousemove', onDocumentMouseMove, false);
document.addEventListener('mousedown', onDocumentMouseDown, false);
document.addEventListener('mouseup', onDocumentMouseUp, false);

let isMouseDown = false;
const mouse = { x: 0, y: 0, prevX: 0, prevY: 0 };
function onDocumentMouseMove(event) {
    mouse.x = event.clientX;
    mouse.y = event.clientY;
    if (isMouseDown) {
        const deltaX = mouse.prevX - mouse.x;
        const deltaY = mouse.prevY - mouse.y;
        cube.rotateY(-deltaX * 0.01);
        cube.rotateX(-deltaY * 0.01);
    }
    mouse.prevX = mouse.x;
    mouse.prevY = mouse.y;
}

function onDocumentMouseDown(event) {
    isMouseDown = true;
}

function onDocumentMouseUp(event) {
    isMouseDown = false;
}

// add light
const spotLight = new THREE.SpotLight(0xeeeece);
spotLight.position.set(1000, 1000, 1000);
scene.add(spotLight);
const spotLight2 = new THREE.SpotLight(0xffffff);
spotLight2.position.set( -200, -200, -200);
scene.add(spotLight2);

// add listener on cube

cube.on('mouseover', ()=>{
    cube.material.color.setHex(0xff0000);
})
cube.on('mouseout', ()=>{
    cube.material.color.setHex(0x00ff00);
})